export const user = async (email: string, password: string): Promise<any> => {
    try {
      return await fetch('http://127.0.0.1:90/api', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify({
            'cmd': 'auth',
            'args': {
                'login': `${email}`,
                'password': `${password}`,
            }
        })
    }).then((res) => res.json())
    } catch (error) {
        console.log(error)
        throw new Error(`${error}`)
    } 
}